"use client";

import { buttonSecondary } from "@/components/Button";
import SearchBar from "@/components/SearchBar";
import { navLinks } from "@/constants";
import Image from "next/image";
import Link from "next/link";
import React, { useEffect, useState } from "react";

const Header = () => {
  /////////////////////////////////////////// MOBILE MENU ////////////////////////////////////////////
  const [mobileOpen, setMobileOpen] = useState(false);

  /////////////////////////////////////////// STICKY TRANSPARENT NAV ////////////////////////////////////////////
  const [isSticky, setSticky] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      setSticky(window.scrollY > 10);
    };

    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  return (
    <header
      className={`sticky top-0 w-full z-10 transitions ${
        isSticky
          ? "bg-primary shadow-sm backdrop-blur-md text-white"
          : "bg-transparent text-primary"
      }`}
    >
      <div className="root-container hidden lg:flex items-center justify-between h-16">
        {/* DESKTOP  */}
        <div className="flex-2 flex items-center gap-8">
          {/* LOGO  */}
          <Link href="/" className="flex items-center gap-1">
            {isSticky ? (
              <Image
                src="/logo/logo2.svg"
                alt="Hudle"
                width={200}
                height={200}
                className="size-7"
              />
            ) : (
              <Image
                src="/logo/logo.svg"
                alt="Hudle"
                width={200}
                height={200}
                className="size-7"
              />
            )}
            <h3 className="hidden lg:block text-2xl font-viga">Hudle</h3>
          </Link>

          {/* DESKTOP NAV  */}
          <nav className="flex items-center gap-6">
            {navLinks.map((link) => (
              <Link
                key={link.name}
                href={link.href}
                className="text-sm font-medium"
              >
                {link.name}
              </Link>
            ))}
          </nav>
        </div>

        {/* SEARCH & LOGIN  */}
        <div className="flex-1 flex items-center gap-8 w-full whitespace-nowrap">
            <SearchBar />
            <div className="flex items-center gap-4">
                <Link href="/login" className="text-sm font-medium">Sign in</Link>
                <Link href="/signup" className={isSticky }>Sign up</Link>
            </div>
        </div>
      </div>
    </header>
  );
};

export default Header;
